// Introspection
// =============

// Brightness
// ----------
/// Determine the brightnss of a color,
/// adjusted for the visual brightnesses
/// of different hues.
/// @group inspect
/// @param {Color} $color -
///   Any color value to be analysed
/// @return {Number} -
///   The relative visual brightness of the color,
///   from 0 (black) through 225959 (white).
@function brightness(
  $color
) {
  // Multiply the primary hues by their respective visual weights
  $red: red($color) * 299;
  $green: green($color) * 587;
  $blue: blue($color) * 114;

  // Collate the resulting total weight
  @return round($red + $green + $blue / 1000);
}

// Harmonies
// =========


// Harmony Weight
// --------------
/// Calculate an appropriate mix-weighting between two colors,
/// based on the relative brightness of the colors.
/// @access private
/// @group internal
/// @param {Color} $color -
///   The base color value, which will be returned
///   with a subtle harmony color mixed in.
/// @param {Color} $harmony -
///   A harmony color to mix into your base color.
/// @param {Percentage} $median -
///   The median amount of your harmony color
///   to mix into your base color,
///   given the same relative brightness.
/// @param {Percentage} $margin -
///   The margin of weight-adjustment
///   for mixing colors with different relative brihtness.
///   A margin of `8%` with a median of `16%`
///   will return mixes weighted between `8%` and `24%` (`16% ± 8%`).
/// @return {Percentage} -
///   A percent-value, weighted above or below the given median,
///   based on the relative visual brightness of the two colors given.
///   Resulting weights are limited to the `0%-50%` range.
@function _harmony-weight(
  $color,
  $harmony,
  $median,
  $margin
) {
  // Get the relative brightness of each color
  $white-bright: brightness(#fff);
  $color-bright: brightness($color);
  $harmony-bright: brightness($harmony);

  // Determine the appropriate mix based on comparative brightness
  $diff: ($harmony-bright - $color-bright) / $white-bright;
  $mix: $median + round($diff * $margin);

  // Cut off mix values outside the 0%-50% range
  $mix: min(50%, $mix);
  $mix: max(0%, $mix);

  // Return the calculated mix weight
  @return $mix;
}


// Harmonize
// ---------
/// Mix a subtle harmony color into your base color,
/// weighted by the relative brightness of the two colors.
/// @group manipulate
/// @param {Color} $color -
///   The base color value, which will be returned
///   with a subtle harmony color mixed in.
/// @param {Color | Function} $harmony ['complement'] -
///   A harmony color to mix into your base color,
///   or the name of a function like `complement` or `invert`
///   that can manipulate the base color and return another color value.
/// @param {Percentage} $median [16%] -
///   The median amount of your harmony color
///   to mix into your base color,
///   given the same relative brightness.
/// @param {Percentage} $margin [8%] -
///   The margin of weight-adjustment
///   for mixing colors with different relative brihtness.
///   A margin of `8%` with a median of `16%`
///   will return mixes weighted between `8%` and `24%` (`16% ± 8%`).
@function harmonize(
  $color,
  $harmony: 'complement',
  $median: 16%,
  $margin: 8%
) {
  @if type-of($harmony) != 'color' {
    // If $harmony is not a color, try running it as a function
    @if function-exists($harmony) {
      $harmony: call($harmony, $color);
    }

    // Error if $harmony is still not a valid color
    @if type-of($harmony) != 'color' {
      @error '$harmony `#{$harmony}` [#{type-of($harmony)}] is not a valid color for harmonizing.';
    }
  }

  // Get the appropriate weighting
  $mix: _harmony-weight($color, $harmony, $median, $margin);

  // Mix a subtle harmony into your color!
  @return mix($harmony, $color, $mix);
}

// Tints & Shades
// --------------

// Shade
// -----
/// Mix a color with black.
/// @group manipulate
/// @param {Color} $color - The initial color to be adjusted.
/// @param {Percentage} $amount - The amount of black to mix in, from `0%-100%`.
/// @return {Color} - your new, darker color!
@function shade(
  $color,
  $amount
) {
  @return mix(#000, $color, $amount);
}

// Tint
// -----
/// Mix a color with white.
/// @group manipulate
/// @param {Color} $color - The initial color to be adjusted.
/// @param {Percentage} $amount - The amount of white to mix in, from `0%-100%`.
/// @return {Color} - your new, lighter color!
@function tint(
  $color,
  $amount
) {
  @return mix(#fff, $color, $amount);
}

@function neutral($color) {
	@if (is-highest-key-value($color)) {
		@if is-high-key-value(complement($color)) {
			@return mix(complement($color), $color, 19%);
		}
		@else {
			@return mix(complement($color), $color, 13%);
		}
	}
	@else if (is-high-key-value($color)) {
		@if is-high-key-value(complement($color)) {
			@return mix(complement($color), $color, 31%);
		}
		@else {
			@return mix(complement($color), $color, 23%);
		}
	}
	@else {
		@if is-highest-key-value(complement($color)) {
			@return mix(complement($color), $color, 31%);
		}
		@if is-high-key-value(complement($color)) {
			@return mix(complement($color), $color, 26%);
		}
		@else {
			@return mix(complement($color), $color, 23%);
		}
	}
}

/*
 * Colors
 */
$color-primary-0: #2E4272;	// Main Primary color - BLUE */
$color-primary-1: #1140AE;
$color-primary-2: #23448F;
$color-primary-3: #2F3B54;
$color-primary-4: #282C37;

$color-secondary-1-0: #AA9439;	// Main Secondary color (1) - YELLOW */
$color-secondary-1-1: #FFCE00;
$color-secondary-1-2: #D6B321;
$color-secondary-1-3: #7E7240;
$color-secondary-1-4: #524D38;

$color-secondary-2-0: #AA7039;	// Main Secondary color (2) - ORANGE */
$color-secondary-2-1: #FF7C00;
$color-secondary-2-2: #D67A21;
$color-secondary-2-3: #7E5E40;
$color-secondary-2-4: #524538;

// Starting colors
$brand-primary: $color-primary-1;
$brand-secondary-1: $color-secondary-1-1;
$brand-secondary-2: $color-secondary-2-1;

// Primary shades
$brand-primary-light: lighten(desaturate($brand-primary, 45%), 25%);
$brand-primary-dark: saturate($brand-primary, 25%);

// Secondary 1 shades
$brand-secondary-1-light: lighten(desaturate($brand-secondary-1, 25%), 40%);
$brand-secondary-1-dark: darken($brand-secondary-1, 25%);

// Secondary 2 shades
$brand-secondary-2-light: lighten(desaturate($brand-secondary-2, 15%), 40%);
$brand-secondary-2-dark: darken($brand-secondary-2, 25%);

// Black & white
$black: darken($brand-primary, 38%);
$white: lighten($brand-primary, 62%);



/*
 * Text
 */
$font-family-sans-serif: 'Concert One', cursive;
$font-family-serif: 'Space Mono', monospace;
$font-size-small: 14px;
$font-size-medium: 16px;
$font-size-large: 20px;
$font-size-x-large: 24px;
$font-size-xx-large: 32px;



/*
 * Layout
 */
$mobile-max-width: 767px;
$tablet-min-width: 768px;
$tablet-max-width: 1024px;

$site-padding-left: 40px;
$site-padding-right: 40px;

$content-padding-top: 20px;
$content-padding-right: 20px;
$content-padding-bottom: 20px;
$content-padding-left: 20px;

$monster-list-width: 200px;


// Mobile
@media (max-width: $mobile-max-width) {
    monster-detail {
        display: flex;
        flex-direction: column;

        .monster-details-card {
            order: 0;
        }

        .monster-details-image {
            order: 1;
            padding-top: $content-padding-top;
            padding-bottom: $content-padding-bottom;
        }

    }
}

// Tablet & Desktop
@media (min-width: $tablet-min-width) {
    monster-detail {
        display: flex;
        flex-direction: row;

        .monster-details-image {
            padding: 0 20px 20px 20px;
        }
    }
}

monster-detail {
    .monster-details-card {
        border: solid 2px $brand-primary-dark;

        .monster-details-card-header {
            background-color: $brand-primary-dark;
            font-family: $font-family-sans-serif;
            text-transform: uppercase;
            font-size: $font-size-large;
            color: $brand-secondary-1;
            padding: 20px 14px 10px;
        }

        dl {
            padding: 20px 14px 10px;
            dt {
                margin-bottom: 6px;
                font-size: $font-size-large;
                font-weight: bold;
            }
            @media (max-width: $mobile-max-width) {
                dt {
                    margin-bottom: 2px;
                    font-size: $font-size-medium;
                    font-weight: bold;
                    text-transform: uppercase;
                }
            }
            dd {
                margin-bottom: 14px;
            }
        }


    }

    // From https://www.sitepoint.com/pure-css3-paper-curls/
    .paper {
        position: relative;
    	// width: 70%;
    	padding: 50px;
    	background-color: #fff;
    	-webkit-box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), inset 0 0 50px rgba(0, 0, 0, 0.1);
    	-moz-box-shadow: 0 0 4px rgba(0, 0, 0, 0.2), inset 0 0 50px rgba(0, 0, 0, 0.1);
    	box-shadow: 0 0 5px rgba(0, 0, 0, 0.2), inset 0 0 50px rgba(0, 0, 0, 0.1);
    }

    .paper:after,
    .paper:before {
        position: absolute;
        width: 40%;
        height: 10px;
        content: ' ';
        left: 12px;
        bottom: 12px;
        background: transparent;
        -webkit-transform: skew(-5deg) rotate(-5deg);
        -moz-transform: skew(-5deg) rotate(-5deg);
        -ms-transform: skew(-5deg) rotate(-5deg);
        -o-transform: skew(-5deg) rotate(-5deg);
        transform: skew(-5deg) rotate(-5deg);
        -webkit-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
        -moz-box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
        box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
        z-index: -1;
    }

    .paper:after {
        left: auto;
        right: 12px;
        -webkit-transform: skew(5deg) rotate(5deg);
        -moz-transform: skew(5deg) rotate(5deg);
        -ms-transform: skew(5deg) rotate(5deg);
        -o-transform: skew(5deg) rotate(5deg);
        transform: skew(5deg) rotate(5deg);
    }

}



